<template>
  <div id="incomings">
    <Header>
      <el-breadcrumb separator-class="el-icon-arrow-right" slot="left">
        <el-breadcrumb-item>收入预算</el-breadcrumb-item>
        <el-breadcrumb-item>收入目标</el-breadcrumb-item>
      </el-breadcrumb>
      <div slot="right">
      </div>
    </Header>
    <div class="content">
      <el-row>
        <el-col :span="4">
          <el-date-picker v-model="yearValue" type="year" placeholder="选择年" @change="selectYearData" format="yyyy 年"
            value-format="yyyy"></el-date-picker>
        </el-col>
        <el-col :span="8" :offset="1">
          <el-input placeholder="输入编号或预算体查询" v-model="keywords" @change="search" clearable>
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="2" :offset="9">
          <el-button type="success" plain size="small" @click="opendialog('add')">新增数据</el-button>
        </el-col>
      </el-row>
      <el-table :data="incomings" border style="width: 100%; margin-top: 20px">
        <el-table-column label="展开" type="expand" width="60px">
          <template slot-scope="props">
            <el-form label-position="left">
              <el-row style="margin-top: 10px;">
                <el-col :span="4">
                  <span>权重: {{ toFixedNum(props.row.weights) }}</span>
                </el-col>
                <el-col :span="4">
                  <span>2区间: {{ toFixedNum(props.row.line2) }}</span>
                </el-col>
                <el-col :span="4">
                  <span>4区间: {{ toFixedNum(props.row.line4) }}</span>
                </el-col>
                <el-col :span="4">
                  <span>6区间: {{ toFixedNum(props.row.line6) }}</span>
                </el-col>
                <el-col :span="4">
                  <span>8区间: {{ toFixedNum(props.row.line8) }}</span>
                </el-col>
                <el-col :span="4">
                  <span>10区间: {{ toFixedNum(props.row.line10) }}</span>
                </el-col>
              </el-row>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="userName" label="姓名"></el-table-column>
        <el-table-column prop="jobnumber" label="工号"></el-table-column>
        <el-table-column prop="period" label="周期"></el-table-column>
        <el-table-column label="类型">
          <template slot-scope="scope">
            {{scope.row.code}} / {{scope.row.typeName}}
          </template>
        </el-table-column>
        <el-table-column label="纵横轴" width="80">
          <template slot-scope="scope">
            {{axisMap[scope.row.axis]}}
          </template>
        </el-table-column>
        <el-table-column prop="qq" label="定量定性">
          <template slot-scope="scope">
            {{qqMap[scope.row.qq]}}
          </template>
        </el-table-column>
        <el-table-column prop="pm" label="正负项">
          <template slot-scope="scope">
            {{pmMap[scope.row.pm]}}
          </template>
        </el-table-column>
        <el-table-column label="目标">
          <template slot-scope="scope">
            {{toFixedNum(scope.row.incomings)}} {{scope.row.unit || '单位'}}
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template slot-scope="scope">
            <el-button size="small" plain type="danger" @click="deleIncomings(scope.row)">删除</el-button>
            <el-button size="small" plain type="success" @click="opendialog('edit', scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination :current-page="page" :page-size="limit" layout="total, prev, pager, next, jumper" :total="count"
        align="right" @current-change="changePage" class="pagi">
      </el-pagination>
    </div>

    <el-dialog :title="titleName" :visible.sync="dialogShow">
      <div>
        <el-form :inline="true" :model="typeData" class="demo-form-inline" :rules="rules" label-width="80px"
          ref="typeform">
          <el-form-item label="姓名" prop="userName">
            <el-input v-model="typeData.userName" :disabled="yes2"></el-input>
          </el-form-item>
          <el-form-item label="工号" prop="jobnumber">
            <el-input v-model="typeData.jobnumber" :disabled="yes2"></el-input>
          </el-form-item>
          <el-form-item label="周期" prop="period">
            <el-input v-model="typeData.period" :disabled="yes2"></el-input>
          </el-form-item>
          <el-form-item label="类型" prop="code">
            <el-select v-model="typeData.code" @change="selectVal" :disabled="yes2">
              <el-option :label="item.name" :value="item.code" v-for="item in typename" :key="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="纵横轴" prop="axis">
            <el-select v-model="typeData.axis" placeholder="纵横轴" :disabled="yes1">
              <el-option label="横轴" :value="1"></el-option>
              <el-option label="纵轴" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="定量定性" prop="qq">
            <el-select v-model="typeData.qq" placeholder="定量定性" :disabled="yes1">
              <el-option label="定量" :value="1"></el-option>
              <el-option label="定性" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="正负项" prop="pm">
            <el-select v-model="typeData.pm" placeholder="正负项" :disabled="yes1">
              <el-option label="正" :value="1"></el-option>
              <el-option label="负" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="单位" prop="unit">
            <el-input v-model="typeData.unit" :disabled="yes1"></el-input>
          </el-form-item>
          <el-form-item label="权重" prop="weights">
            <el-input v-model="typeData.weights"></el-input>
          </el-form-item>
          <el-form-item label="目标" prop="incomings">
            <el-input v-model="typeData.incomings" type="number"></el-input>
          </el-form-item>
          <el-form-item label="2区间" prop="line2">
            <el-input v-model="typeData.line2" type="number"></el-input>
          </el-form-item>
          <el-form-item label="4区间" prop="line4">
            <el-input v-model="typeData.line4" type="number"></el-input>
          </el-form-item>
          <el-form-item label="6区间" prop="line6">
            <el-input v-model="typeData.line6" type="number"></el-input>
          </el-form-item>
          <el-form-item label="8区间" prop="line8">
            <el-input v-model="typeData.line8" type="number"></el-input>
          </el-form-item>
          <el-form-item label="10区间" prop="line10">
            <el-input v-model="typeData.line10" type="number"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogShow=false">取 消</el-button>
        <el-button type="primary" @click="validate">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import Header from '../../components/Header'
  export default {
    name: 'incomings',
    components: {
      Header
    },
    data() {
      return {
        typeMap: {
          0: '系统定义',
          1: '用户自建'
        },
        qqMap: {
          1: '定量',
          2: '定性'
        },
        axisMap: {
          1: '横轴',
          2: '纵轴'
        },
        pmMap: {
          1: '正',
          2: '负'
        },
        yes1: false,
        yes2: false,
        titleName: "",
        typename: [],
        typeData: {
          year: new Date().getFullYear()
        },
        rules: {
          userName: [{
              required: true,
              message: '请输入姓名',
              trigger: 'blur'
            },
            {
              min: 2,
              message: '最少输入2个字符',
              trigger: 'blur'
            }
          ],
          jobnumber: [{
              required: true,
              message: '请输入工号',
              trigger: 'blur'
            },
            {
              min: 2,
              message: '最少输入2个字符',
              trigger: 'blur'
            }
          ],
          period: [{
              required: true,
              message: '请输入周期',
              trigger: 'blur'
            },
            {
              min: 2,
              message: '最少输入2个字符',
              trigger: 'blur'
            }
          ],
          incomings: [{
              required: true,
              message: '请输入目标',
              trigger: 'blur'
            },
          ],
          line2: [{
              required: true,
              message: '请输入数值',
              trigger: 'blur'
            },
          ],
          line4: [{
              required: true,
              message: '请输入数值',
              trigger: 'blur'
            },
          ],
          line6: [{
              required: true,
              message: '请输入数值',
              trigger: 'blur'
            },
          ],
          line8: [{
              required: true,
              message: '请输入数值',
              trigger: 'blur'
            },
          ],
          line10: [{
              required: true,
              message: '请输入数值',
              trigger: 'blur'
            },
          ],
          weights: [{
              required: true,
              message: '请输入权重信息',
              trigger: 'blur'
            },
          ],
          code: [{
            required: true,
            message: '请选择类型',
            trigger: 'change'
          }],
        },
        dialogShow: false,
        incomings: [],
        page: 1,
        limit: 10,
        count: 0,
        year: new Date().getFullYear(),
        keywords: '',
        yearValue: new Date()
      }
    },
    methods: {
      toFixedNum (num) {
        if(num === null || num === '' || num === undefined) return '';
        let isInteger = Number(num || null) % 1 === 0;
        return isInteger ? Number(num) : Number(Number(num).toFixed(2));
      },
      search() {
        this.page = 1;
        this.keywords = this.keywords.trim();
        this.getIncomings()
      }, 
      selectYearData() {
        this.page = 1;
        this.year = new Date(this.yearValue).getFullYear();
        this.getIncomings()
      },
      selectVal(val) {
        let type = this.typename.find(item => {
          return item.code === val;
        })
        this.typeData.axis = type.axis;
        this.typeData.qq = type.qq;
        this.typeData.pm = type.pm;
        this.typeData.unit = type.unit;
      },
      //获取类型
      getTypeLists() {
        this.$http.get(`/ec/api/types?type=incomings`).then(res => {
          let resData = res.data;
          if (resData.errcode === 0) {
            this.typename = resData.data || [];
          }
        })
      },
      getIncomings() {
        this.$http.get(
          `/ec/api/incomings?year=${this.year}&page=${this.page}&limit=${this.limit}&keywords=${this.keywords}`).then(
          res => {
            let resData = res.data;
            if (resData.errcode === 0) {
              this.count = (resData.data.count || 0);
              this.incomings = resData.data.incomings || []
            }
          })
      },
      save() {
        this.$refs.typeform.validate(valid => {
          if (!valid) {
            this.dialogShow = true;
            return;
          }
          let data = this.typeData;
          this.$http.post('/ec/api/incomings', data).then(res => {
            if (res.data.errcode == 0) {
              this.$message.success("保存成功");
              this.dialogShow = false;
              this.typeData = {};
              this.getIncomings();
              return;
            } 
            this.$message.error("保存失败");
            this.dialogShow = false;
          })
        })
      },
      edit() {
        let data = this.typeData;
        this.$http.put('/ec/api/incomings', data).then(res => {
          if (res.data.errcode == 0) {
            this.$message.success("编辑成功");
            this.getIncomings();
          } else {
            this.$message.error("编辑失败");
          }
        })
        this.dialogShow = false;
      },
      opendialog(val, scope) {
        if (val === 'add') {
          this.titleName = "新增收入目标"
          this.yes1 = true;
          this.yes2 = false;
          this.typeData = {};
          this.dialogShow = true;
          this.getTypeLists();
        }
        if (val === 'edit') {
          this.getTypeLists();
          this.titleName = "编辑收入目标";
          this.yes1 = true;
          this.yes2 = true;
          this.typeData = JSON.parse(JSON.stringify(scope));
          this.dialogShow = true;
        }
      },
      validate() {
        if (this.titleName === '新增收入目标') {
          this.save();
        } else {
          this.edit();
        }
      },
      async deleIncomings(row) {
        try {
          let data = {
            year: row.year,
            period: row.period,
            jobnumber: row.jobnumber,
            code: row.code
          };
          let re = await this.$confirm("确定删除吗？");
          if (re == 'confirm') {
            this.$http.delete('/ec/api/incomings', {
              data
            }).then(res => {
              if (res.status == 0) {
                this.$message.success("删除成功");
              }
              this.getIncomings();
            })

          }
        } catch (error) {
          this.$message.success("取消删除");
        }
      },

      changePage(page) {
        this.page = page;
        this.getIncomings()
      }
    },
    created() {
      this.page = this.$route.query.page || 1;
      this.getIncomings()
    }
  }
</script>

<style scoped>
  .pagi {
    margin: 10px;
  }

  .el-form--inline .el-form-item__label {
    color: rgb(94, 94, 94);
    font-weight: bold;
  }

  .el-form--inline .el-form-item {
    margin-right: 25px;
  }

  .el-select>.el-input {
    width: 202px;
  }
</style>